<template>
    <div class="box" @scroll="scroll()" ref="box">
        <div class="my-set clearfix" ref="show">
            <div class="fr">
                <img src="../../assets/images/cuo.png" alt="" class="auto-img" @click="outLogin" >
            </div>
            
        </div>
        <div class="my-login clearfix">
            <div class="fl login-img">
                <img src="../../assets/images/touxiang.png" alt="" class="auto-img">
            </div>
            <div class="fl login-center"  @click="go({name:'Login'})" v-if="!isLogin">
                <span>登录</span>/<span>注册</span>
            </div>
            <div class="fl login-center" v-else>
                {{username}}
            </div>
            <div class="fl login-vip">
                <img src="../../assets/images/my-vip.png" alt="" class="auto-img">
            </div>
        </div>
        <div class="balance clearfix">
            <div v-for="(item,index) in balanceData" :key="index" class="fl">
                <div>{{item.count}}</div>
                <div>{{item.title}}</div>
            </div>
        </div>
        <div class="my-vip clearfix">
            <div class="vip-img fl">
                <img src="../../assets/images/vip.png" alt="" class="auto-img">
            </div>
            <div class="fl vip-msg">
                尊享特权<span>·每月立省168元</span>
            </div>
            <div class="fr vip-right">
                <img src="../../assets/images/my-right.png" alt="" class="auto-img">
            </div>
        </div>
        <div class="my-cost">
            <div class="clearfix">
                <div class="fl">
                    <div class="cost-today">今日充值赠返</div>
                    <div class="cost-good">充值赠好礼</div>
                </div >
                <div class="fr cost-pay">充值</div>
            </div>
            <div class="cost-pay-all clearfix">
                <div class="fl">消费记录</div>
                <div class="fl">充值记录</div>
                <div class="fl">打赏记录</div>
            </div>
        </div>
        <div class="my-msg clearfix">
            <div class="msg-one fl" >
                <div class="msg-one-img">
                    <img class="auto-img" src="../../assets/images/my-msg.png" alt="">
                </div>
                <div class="msg-one-info">消息</div>  
            </div>
            <div class="msg-one fl" >
                <div class="msg-one-img">
                    <img class="auto-img" src="../../assets/images/my-collect.png" alt="">
                </div>
                <div class="msg-one-info">收藏</div>
            </div>
            <div class="msg-one fl" >
                <div class="msg-one-img">
                    <img class="auto-img" src="../../assets/images/my-shuping.png" alt="">
                </div>
                <div class="msg-one-info">书评</div>
            </div>
            <div class="msg-one fl" >
                <div class="msg-one-img">
                    <img class="auto-img" src="../../assets/images/my-time.png" alt="">
                </div>
                <div class="msg-one-info">阅历</div>
            </div>
        </div>
        <div class="my-list">
            <div class="my-list-one clearfix" v-for="(item,index) in listData" :key="index">
                <div class="fl">{{item}}</div>
                <div class="fr">
                    <img src="../../assets/images/my-right.png" alt="" class="auto-img">
                </div>
            </div>
            
        </div>
        
    </div>
</template>

<script>
import { Dialog } from 'vant';
    

    export default {
        data(){
            return{
                balanceData:[
                    {title:"书豆",count:0},
                    {title:"豆数",count:0},
                    {title:"章节卷",count:0},
                    {title:"月票",count:0},
                ],
                msgData:[
                    {title:"消息",icon:"../../assets/images/my-msg.png"},
                    {title:"收藏",icon:"../../assets/images/my-collect.png"},
                    {title:"书评",icon:"../../assets/images/my-shuping.png"},
                    {title:"阅历",icon:"../../assets/images/my-time.png"},
                ],
                listData:[
                    "我的书籍",
                    "我要写作",
                    "阅读喜好",
                    "意见反馈",
                    "礼品中心",
                    "公益3小时"
                ],
                isLogin:false,
                username:""
            }
        },
        created(){
            if(JSON.parse(localStorage.getItem("login_TK"))){
                this.isLogin=true
                this.username = JSON.parse(localStorage.getItem("login_TK")).username
            }
        },
        methods:{
            scroll(){
                let scrollY = this.$refs.box.scrollTop
                let a = scrollY/100 > 1 ? 1 : scrollY/100
                document.querySelector(".my-set").style.backgroundColor='rgba(255, 255, 255, '+ a +')';
            },
            go(e){
                this.$router.push(e)
            },
            outLogin(){
                

                Dialog.confirm({
                    title: '',
                    message: '确认退出登录?',
                })
                .then(() => {
                    localStorage.removeItem("login_TK");
                    this.$router.push({ name: "Login" });
                })
                .catch(() => {
                    
                });
            }
            
           
        }
    }
</script>

<style lang="less" scoped>

.box{
    height: 667px;
    overflow-y:scroll ;
    // overflow: hidden;
}
.box::-webkit-scrollbar {
    display: none;
}
.my-set{
    width: 100%;
    position: fixed;
    background-color:rgba(255, 255, 255, 0);
    >div{
        width: 15px;
        padding: 10px;
    }
}
.my-login{
    padding-top: 50px;
    margin-left: 15px;
    .login-img{
        width: 80px;
    }
    .login-center{
        font-weight: 700;
        font-size: 18px;
        margin-top: 25px;
        margin-left: 15px;
    }
    .login-vip{
        width: 20px;
        margin-top: 25px;
        margin-left: 10px;
    }
}
.balance{
    padding: 20px 0;
    >div{
        width: 25%;
        text-align: center;
        >div:first-child{
            font-size: 22px;
            font-weight: 700;
            color: #6c6e70;
        }
        >div:last-child{
           color: #ddd;
        }
    }
}
.my-vip{
    width: 90%;
    margin: 5px auto;
    background-color: #eec39c;
    height: 50px;
    border-radius: 10px ;
    .vip-img{
        width: 40px;
        margin-top: 5px;
        margin-left: 15px;
    }
    .vip-msg{
        font-size: 16px;
        font-weight: 700;
        margin-top: 13px;
        margin-left: 25px;
        >span{
            font-size: 14px;
            color: #bf8d68;
        }
    }
    .vip-right{
        width: 20px;
        background-color: black;
        border-radius: 50%;
        margin-right: 20px;
        margin-top: 15px;
    }
}
.my-cost{
    border-radius: 10px;
    width: 90%;
    margin: 10px auto;
    background-color: #fff;
    box-sizing: border-box;
    padding: 20px;
    .cost-today{
        font-weight: 700;
        font-size: 16px;
    }
    .cost-good{
        font-weight: 700;
        color: #f4d2b2;
    }
    .cost-pay{
        width: 70px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        color: #fff;
        background-color: #21b382;
        border-radius: 15px;
    }
    .cost-pay-all{
        width: 100%;
        margin-top: 20px;
        >div{
            width: 33%;
            text-align: center;
            color: #959696;
            border-right: 1px solid #ccc;
        }
        >div:last-child{
            border-right: none;
        }
    }
}
.my-msg{
    border-radius: 10px;
    width: 90%;
    margin: 10px auto;
    background-color: #fff;
    box-sizing: border-box;
    padding: 20px 0;
    .msg-one{
        width: 25%;
        text-align: center;
    }
    .msg-one-img{
        width: 30%;
        margin-left: 30px;
        margin-bottom: 5px;
    }
    .msg-one-info{
        color: #ccc;
    }
}
.my-list{
    width: 90%;
    margin: 0 auto;
    background-color: #fff;
    padding: 10px 15px;
    border-radius: 15px;
    box-sizing: border-box;
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 65px;
    .my-list-one{
        padding: 10px 0;
        >div:last-child{
            width: 20px;
        }
    }
}
</style>